import axios from 'axios';

export const getLastOrder = () => {
  return axios.get('http://ccmobile.deptcpanel.princeton.edu/coffee/customer/orderinfo')
      .then(res => {
        return res.data[0];
      });
}

export const getAllItems = () => {
  return axios.get('http://ccmobile.deptcpanel.princeton.edu/coffee/customer/menu')
      .then(res => {
        return res.data;
      })
}

export const postMakeOrder = (update) => {
  axios.post('http://ccmobile.deptcpanel.princeton.edu/coffee/customer/makeorder', update)
      .then(res => {
     });
}
